.recipe {
    background: white;
    -webkit-box-shadow: 7px 7px 5px 0px rgba(195,195,199,1);
    box-shadow: 7px 7px 5px 0px rgba(195,195,199,1);
    height: 100%;
    position: relative;
}

img {
    width: 100%;
    height: 100%;
    cursor: pointer;
}


.recipe-info {
    padding-left: 10px;
    max-height: 0;
    -webkit-transition: max-height .2s ease;
    -o-transition: max-height .2s ease;
    transition: max-height .2s ease;
    overflow: hidden;
}

.overlay {
    position: absolute;
    margin: auto;
    font-size: 24px;
    text-align: center;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    background-color: rgba(30, 50 ,56, 0.5);
    display: none;
}

.recipe:hover .overlay {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
}

.showInfo {
    max-height: 5000px;
    -webkit-transition: max-height 1s ease;
    -o-transition: max-height 1s ease;
    transition: max-height 1s ease;
}

li {
    list-style-type: disc;
}

ul {
    padding-left: 25px;
}


