.nav-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px 0;
    background: #263238;
    color: white;
}

li {
    list-style: none;
}

.nav-container h3 {
    margin-left: 25px;
}

.nav-container ul {
    display: -ms-flexbox;
    display: flex;
}

.nav-container ul li {
    margin-right: 25px;
}

.nav-container ul li a, #mobile-menu li a {
    font-size: 16px;
    color: white;
    text-decoration: none;
    -webkit-transition: all .25s ease;
    -o-transition: all .25s ease;
    transition: all .25s ease;
}

ul li a:hover, #mobile-menu li a:hover {
    color: #2196f3;
}

#hamburger {
    display: none;
}

#mobile-menu {
    z-index: 2;
    text-align: center;
    background: #263238;
    margin: 0;
    max-height: 5000px;
    -webkit-transition: all;
    -o-transition: all;
    transition: all;
    -webkit-transition-duration: .1s;
         -o-transition-duration: .1s;
            transition-duration: .1s;
    overflow: hidden;
}

#mobile-menu li {
    margin-bottom: 5px;
}

.bar-container {
    margin-top: 8px;
    cursor: pointer;
}

.bar-container:hover .bar {
    background-color: #2196f3;
}

.bar {
    width: 35px;
    margin-bottom: 8px;
    height: 3px;
    border-radius: 20px;
    background: white;
    -webkit-transition: all .3s ease;
    -o-transition: all .3s ease;
    transition: all .3s ease;
}

@media(max-width: 600px) {
    .nav-container ul li:not(#hamburger) {
        display: none;
    }
    
    #hamburger {
        display: block;
    }
    
}

@media(min-width: 600px) {
    #mobile-menu {
        opacity: 0 !important;
        height: 0 !important;
    }
}



