.modal {
    width: 45%;
    height: 80%;
    text-align: center;
    margin: auto;
    background: white;
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    background: gray;
    color: white;
    overflow: scroll;
    padding-bottom: 25px;
    word-wrap: break-word;
}

.modal-header {
    padding: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-weight: normal;
    background: #263238;
}

textarea {
    border-radius: 6px;
    width: 71%;
    height: 60%;
}

a {
    text-decoration: none;
    color: white;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
}

i {
    cursor: pointer;
}

i:hover {
    color: blue;
}

.recipe-form {
    padding: 0 15px;
    
}

.form-group {
    margin-bottom: 10px;
}


input {
    height: 15px;
    padding: 10px 5px;
    width: 70%;
    text-align: left;
    border-radius: 6px;
}

#submit {
    border: 2px solid white;
}

#cancel {
    border: 2px solid red;
}

#submit, #cancel {
    border-radius: 10px;
    padding: 15px 25px;
    margin: 10px;
}

#buttons {
    margin-top: 40px;
}

#submit:hover {
    background-color: white;
    color: black;
}

#cancel:hover {
    background-color: red;
    color: black;
}

textarea:focus, input:focus{
    outline: none;
}

@media(max-width: 600px) {
    .modal {
        width: 85%;
    }
}


@media(min-width: 600px) and (max-width: 900px) {
    .modal {
        width: 55%;
    }
}



